p, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

body {
  background-color: #0ff;
}

.uk {
  width: 356px;
}

.shopping-list-section {
  gap: 40px;
  margin-top: 40px;
  display: flex;
}

.shopping-div-svg {
  margin-bottom: 40px;
}

.is-hidden {
  display: none;
}

.shopping-list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shop-img {
  border-radius: 8px;
  padding: 24px;
}

.shopping-list-item {
  width: 996px;
  height: 213px;
  background-color: #fff;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  display: flex;
}

.shop-item-div {
  padding: 24px 24px 24px 0;
}

.shopping-list-text {
  width: 808px;
  height: 53px;
  margin-bottom: 32px;
  font-size: 14px;
  line-height: 18px;
}

.shopping-list-category {
  margin-bottom: 14px;
}

.shopping-list-author {
  margin-top: auto;
}

.book-links {
  gap: 16px;
  display: flex;
}

.js-remove {
  width: 38px;
  height: 38px;
  background-color: #4f2ee8;
  border: none;
  border-radius: 50%;
}

.author-icons {
  justify-content: space-between;
  display: flex;
}

.top-item-div {
  justify-content: space-between;
  margin-bottom: 4px;
  display: flex;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    margin: 0 auto;
  }

  .hidden {
    display: none;
  }

  .uk {
    width: 0;
  }

  .shopping-list-section {
    justify-content: center;
  }

  .shop-img {
    padding: 14px;
  }

  .shopping-list-category {
    margin-bottom: 8px;
  }

  .shopping-list-item {
    width: 335px;
    height: 170px;
  }

  .shop-item-div {
    padding: 14px 14px 14px 0;
  }

  .shopping-list-text {
    width: 193px;
    margin-bottom: 14px;
  }

  .js-remove {
    width: 28px;
    height: 28px;
  }

  .top-item-div {
    margin-bottom: 4px;
  }

  .shop-img {
    width: 100px;
    height: 142px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }

  .shop-img {
    border-radius: 8px;
    padding: 24px;
  }

  .shopping-list-item {
    width: 704px;
    height: 213px;
  }

  .js-remove {
    width: 38px;
    height: 38px;
  }

  .shopping-list-text {
    width: 508px;
  }

  .shop-img {
    width: 116px;
    height: 165px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1392px;
    padding: 0 24px;
  }

  .hidden {
    display: block;
  }
}

